<template>
    <div>
        <Annotator :options="args.data" @ui-updated="onUIUpdated" @submit-data="submitData">
        </Annotator>
    </div>
</template>

<script>
import { ref } from "vue"
import { Streamlit } from "streamlit-component-lib"
import { useStreamlit } from "./streamlit"
import Annotator from "./Annotator.vue"

export default {
    name: "MyComponent",
    props: ["args"], // Arguments that are passed to the plugin in Python are accessible in prop "args"
    components: {Annotator},
    created(){
        // console.log("received args", this.args.data)
        useStreamlit() // lifecycle hooks for automatic Streamlit resize
    },
    methods: {
        onUIUpdated() {
            console.log("updating")
            setTimeout(() => {
                Streamlit.setFrameHeight()
            }, 0)
        },
        submitData(data) {
            console.log(data)
            Streamlit.setComponentValue(JSON.stringify(data));
        }
    }
}
</script>
